@font-face {
  font-family: "BalsamiqSans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/BalsamiqSans-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "BalsamiqSans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/BalsamiqSans-Bold.woff2") format("woff2");
}

